import {Component} from '@angular/core';
import {HelpersService} from '../helpers.service';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Storage} from "@ionic/storage";
import {ContentComponent} from "../content/content.component";
import {ModalController} from "@ionic/angular";
import {Router} from "@angular/router";

@Component({
    selector: 'app-tab1',
    templateUrl: 'tab1.page.html',
    styleUrls: ['tab1.page.scss'],
})
export class Tab1Page {
    posts: any;
    path: string;
    private itemsCollection: AngularFirestoreCollection;

    constructor(
        private helper: HelpersService,
        private db: AngularFirestore,
        private storage: Storage,
        private modalCtrl: ModalController,
        public router: Router) {
        this.ionViewWillEnter();
    }

    ionViewWillEnter() {
        this.storage.get('domainPath').then(path => {
            //Quick-fix: For bug when starting up for the first time.
            if (path == null) {
                this.ionViewWillEnter();
                return;
            }
            //Make sure that the tab is updated with correct domain,
            // but only refresh content if domain has changed.
            if (this.path !== path) {
                this.path = path;
                this.itemsCollection = this.posts = this.db.collection(path + 'news', ref => ref.orderBy('startTime'));
                this.posts = this.itemsCollection.valueChanges();
                this.path = path;
            }
        });
    }

    async showContentModal(data: object) {
        console.log(data);
        // const oldData: any = {...data};
        const modal = await this.modalCtrl.create(
            {
                component: ContentComponent,
                componentProps: {
                    data: {...data}
                }
            }
        );
        await modal.present();
        modal.onDidDismiss().then((e) => {
            if (e.data) {
                console.log("WE NEED TO UPDATE! ü§Ø", e.data);
                //TODO: Do the update procedure! Yes sir!üëÆ‚Äç
                //Something like...
                // this.itemsCollection.doc(this.path + 'news/'+documentID).update(e.data)
            this.updateItem(e.data);
            }
        });
    }

    async createNewPush() {
        const modal = await this.modalCtrl.create(
            {
                component: ContentComponent,
                componentProps: {
                    // data: {newThing: true}
                }
            }
        );
        await modal.present();
        modal.onDidDismiss().then((e) => {
            if (e.data) {
                console.log("SEND THIS TO THE SERVER!", e.data);
                this.addItem(e.data);
            }
        });
    }

    goToUserPage() {
        this.router.navigate(['user'])
    }

    private addItem(item: object) {
        this.itemsCollection.add(item);
    }

    private updateItem(item: object) {
        // this.itemsCollection.snapshotChanges().doc(item.id).update(item)
        // console.log(item);
    }
}
